body {
    @include body;
    background-color: white;
    color: white;
    font-family: 'Raleway', sans-serif;
}

h1 {
    font-family: sans-serif;
}

/* *************************************************
                        HEADER
************************************************* */
#header {
    background: linear-gradient(90deg, $blue, #131D27);

    .content-in {
        display: grid;
        place-items: center;
        gap: 16px;

        @media screen and (min-width: 768px) {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        nav.main-menu {
            display: flex;

            .btn-menu {
                position: relative;
                cursor: pointer;

                &:not(:last-of-type) {
                    margin-right: 32px;
                }

                @media screen and (min-width: 768px) {
                    &:not(:first-of-type) {
                        &::after {
                            content: '';
                            position: absolute;
                            top: 0;
                            left: -16px;
                            width: 1px;
                            height: 100%;
                            background: white;
                        }
                    }
                }
            }
        }
    }
    
}

/* *************************************************
                        HERO
************************************************* */

#hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: url('../images/hero.png') no-repeat center right;
    background-size: cover;
    height: 300px;

    .content-in {
        
    }

    .wrapper {
        max-width: 250px;
        h1 {
            margin-top: 0;
            padding: 8px;
            border-top: 2px solid $blue;
            border-bottom: 2px solid $blue;
            color: $blue;
            font-size: 26px;
        }

        h2 {
            color: #373435;
            font-size: 18px;
            font-weight: 800;
        }
    }
}
